import React from 'react'
import auth from './lib/authentication.js'

import App from './App'
import LoginPage from './modules/LoginPage'
import MenuPage from './modules/Menu/MenuPage'
import EventPage from './modules/EventPage'
import MessagePage from './modules/MessagePage'
//import AlertPage from './modules/Alerts'
import AlertPage from './modules/Alerts/test'


function redirectToLogin(nextState, replace) {
  if (!auth.loggedIn()) {
    replace({
      pathname: '/login',
      state: { nextPathname: nextState.location.pathname }
    })
  }
}

export default {
  path: '/',
  component: App,
  childRoutes: [
    {
      path: 'test',
      component: <h2>Test</h2>
    },
    {
      path: 'login',
      component: LoginPage
    },
    {
      onEnter: redirectToLogin,
      childRoutes: [
        // Protected routes

        // MENU
        {
          path: 'menu',
          component: MenuPage
        },

        // ALERTS
        {
          path: 'alerts',
          component: AlertPage
        },

        // MESSAGE
        {
          path: 'message',
          component: MessagePage
        },

        // EVENTS
        {
          path: 'events',
          component: EventPage
        }
      ]
    }
  ]
}


